@media only screen and (max-width: 1000px) {
    .aboutUsContent {
        flex-direction: column !important;
    }

    .aboutUsPerson {
        flex-direction: row !important;
    }

    .aboutUsImage {
        width: 50% !important;
    }
}

.aboutUsContent {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-evenly;
}

.aboutUsPerson {
    flex: 0.2;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 10px;
}

.aboutUsImage {
    height: auto;
    width: 100%;
    border-radius: 100%;
}

.aboutUsName {
    font-family: Spaceship;
    font-size: 20px;
    text-align: center;
    margin-top: 5px;
}

.aboutUsText {
    text-align: center;
}